import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import HttpBackend from 'i18next-http-backend'; // Pentru încărcarea traducerilor prin HTTP
import LanguageDetector from 'i18next-browser-languagedetector'; // Pentru detectarea limbii browserului

i18n
  .use(HttpBackend)  // încarcă traducerile folosind backend HTTP
  .use(LanguageDetector)  // detectează limba din browser
  .use(initReactI18next)  // inițializează react-i18next
  .init({
    fallbackLng: 'ro', // Limba de rezervă, folosită când nu este disponibilă limba detectată
    supportedLngs: ['ro', 'ru'], // Limbile suportate
    preload: ['ro', 'ru'], // Preîncarcă limbile specificate pentru o performanță îmbunătățită
    detection: {
      order: ['localStorage', 'cookie', 'navigator', 'querystring', 'htmlTag'], // Ordinea metodelor de detectare
      caches: ['localStorage'], // Limba selectată va fi păstrată în localStorage
      lookupLocalStorage: 'i18nextLng', // Cheia sub care se salvează limba în localStorage
      lookupCookie: 'i18n_lng', // Numele cookie-ului pentru limbă
      cookieMinutes: 10080, // Timpul de expirare a cookie-ului (7 zile)
      htmlTag: document.documentElement, // Utilizează tag-ul html pentru a seta atributul lang
    },
    keySeparator: false, // Dezactivează utilizarea separatorului pentru chei
    interpolation: {
      escapeValue: false, // Nu este necesară escaparea valorilor în React
    },
    backend: {
      loadPath: '/translations/{{lng}}/{{ns}}.json', // Calea către fișierele de traduceri
      reloadInterval: false, // Nu reîncărca resursele de limbă periodic
      onLoad: (response) => console.log('Load response:', response) // Loghează răspunsul la încărcarea traducerilor
    },
    react: {
      useSuspense: true, // Dezactivează Suspense pentru încărcarea traducerilor
      // wait: true // Așteaptă încărcarea resurselor de limbă înainte de a randa componentele
    },
    debug: false, // Activează modul debug pentru a vedea loguri detaliate în consola browserului
  });

export default i18n;
